import { Link } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";
const HostName = require("./../HostName/hostname");
const Navigation = () => {
  const [itemList, setItemList] = useState([]);

  useEffect(() => {
    axios
      .get(`${HostName}/home`)
      .then((response) => {
        setItemList(response.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <>
      <div className="blogmain-head-block">
        <div className="blogmain-heading-mb">
          <div className="blogmain-heading">
            <p>The</p>
            <p>Siren</p>
          </div>
          <div className="mb-menu">
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
        <div className="blogmain-navigation-bar">
          <ul>
            <>
              {itemList.map((items, index) => (
                <li key={items.id}>
                  <Link to={`/${items.item}`} className="navlink-normal">
                    {items.item}
                    <hr className="hidden" />
                  </Link>
                </li>
              ))}
            </>
          </ul>
          <hr />
        </div>
      </div>
    </>
  );
};

export default Navigation;
